import { zIndexManage } from "@/utils/zIndexManage";
import { createSlice, PayloadAction } from "@reduxjs/toolkit";

const SYSTEM_NAME: string = process.env.REACT_APP_NAME ?? "test";

interface MaskState {
  loadingQueue: any[];
  maskQueueArr: any[];
  zIndex: number;
  caller: string;
}

const initialState: MaskState = {
  loadingQueue: [],
  maskQueueArr: [],
  zIndex: zIndexManage.$zindexAntdModal,
  caller: "",
};

export const maskSlice = createSlice({
  name: "mask",
  initialState,
  reducers: {
    setMaskArr: (state, action: PayloadAction<any>) => {
      state.maskQueueArr = [...state.maskQueueArr, action.payload];
      state.zIndex += 1;
    },
    setClearMaskArr: (state, action: PayloadAction<any>) => {
      state.maskQueueArr = state.maskQueueArr.filter((item) => {
        return item !== action.payload;
      });
      state.zIndex -= 1;
    },
    setClearMaskArrAll: (state) => {
      state.maskQueueArr = [];
      state.zIndex = zIndexManage.$zindexModalBackdrop;
    },
    setLoading: (state, action: PayloadAction<any>) => {
      state.loadingQueue = [...state.loadingQueue, action.payload.record];
      state.caller = action.payload.caller;
    },
    clearLoading: (state, action: PayloadAction<any>) => {
      const key = action.payload.key;
      const temp = state.loadingQueue.filter((obj) => {
        return obj.key !== key;
      });
      state.loadingQueue = temp;
      state.caller = action.payload.caller;
    },
    clearAllLoading: (state, action: PayloadAction<string>) => {
      state.loadingQueue = [];
      state.caller = action.payload;
    },
  },
});

export const {
  setMaskArr,
  setClearMaskArr,
  setClearMaskArrAll,
  setLoading,
  clearLoading,
  clearAllLoading,
} = maskSlice.actions;

export const selectLoading = (state: any) => {
  return state?.[SYSTEM_NAME]?.maskReducer?.loadingQueue;
};

export const selectMaskArr = (state: any) => {
  return state?.[SYSTEM_NAME]?.maskReducer?.maskQueueArr;
};

export const selectMaskZIndex = (state: any) => {
  return state?.[SYSTEM_NAME]?.maskReducer?.zIndex;
};

export default maskSlice.reducer;
