import React from "react";
import styled from "styled-components";
import Code from "@/components/Code";
import { Divider } from "antd";

const ContentContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 48px;
  width: 100%;
  background-color: white;
  gap: 10px;
  margin-left: 200px;
`;

const Notice = styled.div`
  background-color: rgb(246, 247, 249);
  border-color: rgb(229, 231, 235);
  border-radius: 16px;
  border-style: solid;
  border-width: 0px;
  border-image-outset: 0;
  border-image-repeat: stretch;
  border-image-slice: 100%;
  border-image-source: none;
  border-image-width: 1;
  box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(0, 0, 0, 0) 0px 0px 0px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px inset;
  box-sizing: border-box;
  color: rgb(64, 71, 86);
  margin-bottom: 32px;
  margin-top: 32px;
  overflow-wrap: break-word;
  padding-bottom: 16px;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 24px;
`;

const NoticeUl = styled.ul`
  margin-inline-start: 1.5rem;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
`;
const NoticeLi = styled.li`
  margin-bottom: 0.25rem;
`;

const Space = styled.div`
  padding: 24px;
`;

const IncludeCode = `
  <html lang="en">
    <head>
      ...
    </head>
    <body>
      ...
      <script src="scan.js"></script>
    </body>
  </html>
`;

const InitCode = `
...
const MyScan = new WebFxScan();
connectServer();
async function connectServer() {
  await MyScan.connect({ ip: "127.0.0.1", port: "17778" });
  await MyScan.init();
}
...`;

const DeviceCode = `
...
const { deviceName } = getScanner();
async function getScanner() {
  return await MyScan.getDeviceList();
}
...`;

const ConfigCode = `
...
const scannerConfig = {
  deviceName: "K81U",
  source: "ADF-Back",
  paperSize: "A4",
  resolution: 300,
  mode: "lineart",
  brightness: 0,
  contrast: 0,
  quality: 75,
}
readyScanner(scannerConfig);
async function readyScanner(config) {
  await MyScan.setScanner(config);
}
...`;

const ScanCode = `
...
const { result, message, data, error } = scan();
if(result) {
  data.map((file)=> {
    const { fileName, base64, ocrText } = file;
    console.log(fileName, base64);
    console.log(ocrText) // 印出辨識文字，如果有的話
  })
} else {
  console.log(error);
}

async function scan() {
  return await MyScan.scan();
}
...`;

const GetFileList = `
...
const { result, message, data, error } = getList();
if(result) {
  data.map((file)=> {
    const { fileName, base64, ocrText } = file;
    console.log(fileName, base64);
    console.log(ocrText) // 印出辨識文字，如果有的話
  })
} else {
  console.log(error);
}

async getList() {
  return await MyScan.getFileList();
}
...`;

const AutoScan = `
...
const { result, message, error } = autoScan();
if(!result) {
  console.log(error);
}

const callback = (data) => {
  // 當紙張
  console.log(data);
};

async autoScan() {
  await MyScan.autoScan(callback);
  await MyScan.getFileList();
}
...`;

const QuickStart: React.FC = () => {
  // Your component code here
  const { LIB_DOC_CONF } = window as any;
  const { libVersion, serverWinVersion, serverLinuxVersion } = LIB_DOC_CONF;

  return (
    <>
      <ContentContainer>
        <h1>Quick Start</h1>
        <p>
          歡迎使用WebFXScan JS lib，這裡會介紹基礎使用，以及一些情境下的範例。
        </p>
        <Notice>
          <h3>注意，使用此lib之前你需要確認的事項:</h3>
          <NoticeUl>
            <NoticeLi>是否已經安裝掃描器與驅動</NoticeLi>
            <NoticeLi>是否已經啟動WebFXScan Server</NoticeLi>
            <NoticeLi>
              此LIB的行為大多為網路操作，返回格式為promise，這裡以ES6後支援的async/await語法作為舉例
            </NoticeLi>
            <NoticeLi>此LIB版本為: {libVersion}</NoticeLi>
            <NoticeLi>對應的windows server版本為: {serverWinVersion}</NoticeLi>
          </NoticeUl>
        </Notice>
        <h2>導入</h2>
        <p>首先您需要先將下載的scan.js文件包含在頁面中。例子：</p>
        <Code code={IncludeCode} language="html" />
        <Space />
        <h2>連接與初始化</h2>
        <p>
          假定環境已經就緒，接下來就要實例物件並嘗試與server連線，在你的script中插入
        </p>
        <Code code={InitCode} language="javascript" />
        <Space />
        <h2>設置掃描器參數</h2>
        在執行掃描之前，你需要先取得連接的掃描器資訊
        <Code code={DeviceCode} language="javascript" />
        通知server設置你的掃描器參數，此處以PT2160掃描器作為範例
        <Code code={ConfigCode} language="javascript" />
        <Space />
        <h2>進行掃描</h2>
        執行掃描，並且印出掃描後的檔名與base64
        <Code code={ScanCode} language="javascript" />
        <Space />
        <Divider />
        <h1>其他情境操作</h1>
        <h2>取得掃瞄過的所有圖檔</h2>
        <Code code={GetFileList} language="javascript" />
        <Space />
        <h2>設置自動掃描</h2>
        當紙張放入掃描機後，自動進行掃描，因此這邊需要傳入callback使用
        <Code code={AutoScan} language="javascript" />
        <Space />
      </ContentContainer>
    </>
  );
};

export default QuickStart;
