import { useState, useEffect } from "react";
import styled from "styled-components";
import { Table } from "antd";
import type { ColumnsType } from "antd/es/table";

interface TableDataType {
  key: React.Key;
  argument: string;
  describe: string;
  type: string;
  default: string;
}

interface ArguData {
  arguName: string;
  fields: TableDataType[];
}

interface ComponentProps {
  id: string;
  fullMethod: string;
  methodDescribe: string;
  version: string;
  propertyList: any[];
  returnValue: any[];
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const APITitle = styled.h2`
  background-color: #e6e6e6ad;
  border-radius: 6px;
  padding: 10px;
  margin-left: -10px;
`;

const Space = styled.div`
  padding: 24px;
`;

const columns: ColumnsType<TableDataType> = [
  {
    title: "參數",
    dataIndex: "argument",
    width: 200,
  },
  {
    title: "說明",
    dataIndex: "describe",
    // render: (text) => <div style={{ whiteSpace: "pre-line" }}>{text}</div>,
  },
  {
    title: "型別",
    dataIndex: "type",
    width: 200,
  },
  {
    title: "預設值",
    dataIndex: "default",
    width: 200,
  },
];

const APIField = (props: ComponentProps) => {
  const {
    id,
    fullMethod,
    methodDescribe,
    version,
    propertyList = [],
    returnValue = [],
  } = props;
  useEffect(() => {}, []);

  return (
    <>
      <div>
        <Space id={id} />
        <Container>
          <APITitle>{fullMethod}</APITitle>
          <p>{methodDescribe}</p>
          <h3>Since</h3>
          <p>{version}</p>
          <h3>Arguments</h3>
          {propertyList.length === 0 ? (
            <p>無</p>
          ) : (
            propertyList.map((item: ArguData) => {
              const { arguName, fields = [] } = item;
              if (fields.length > 0) {
                return (
                  <>
                    <label>{arguName}</label>
                    <Table
                      columns={columns}
                      dataSource={fields}
                      pagination={false}
                      bordered
                    />
                  </>
                );
              } else {
                return (
                  <>
                    <label>{arguName}</label>
                  </>
                );
              }
            })
          )}
          <h3>Return</h3>
          {returnValue.length === 0 ? (
            <p>無</p>
          ) : (
            returnValue.map((item: ArguData) => {
              const { arguName, fields = [] } = item;
              if (fields.length > 0) {
                return (
                  <>
                    <label>{arguName}</label>
                    <Table
                      columns={columns}
                      dataSource={fields}
                      pagination={false}
                      bordered
                    />
                  </>
                );
              } else {
                return (
                  <>
                    <label>{arguName}</label>
                  </>
                );
              }
            })
          )}
        </Container>
      </div>
    </>
  );
};

export default APIField;
