import { useState, useEffect } from "react";
import styled from "styled-components";
import { Table } from "antd";
import type { ColumnsType } from "antd/es/table";

interface TableDataType {
  key: React.Key;
  argument: string;
  describe: string;
  type: string;
  default: string;
}

interface ArguData {
  arguName: string;
  fields: TableDataType[];
}

interface ComponentProps {
  id: string;
  name: string;
  methodDescribe: string;
  fields: any[];
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const Space = styled.div`
  padding: 24px;
`;

const columns: ColumnsType<TableDataType> = [
  {
    title: "參數",
    dataIndex: "argument",
    width: 200,
  },
  {
    title: "說明",
    dataIndex: "describe",
    // render: (text) => <div style={{ whiteSpace: "pre-line" }}>{text}</div>,
  },
  {
    title: "型別",
    dataIndex: "type",
    width: 200,
  },
  {
    title: "預設值",
    dataIndex: "default",
    width: 200,
  },
];

const TypeField = (props: ComponentProps) => {
  const { id, methodDescribe, name, fields } = props;
  useEffect(() => {}, []);

  return (
    <>
      <div>
        <Space id={id} />

        <Container>
          <h2>{name}</h2>
          <p>{methodDescribe}</p>
          <Table
            columns={columns}
            dataSource={fields}
            pagination={false}
            bordered
          />
        </Container>
      </div>
    </>
  );
};

export default TypeField;
