import React, { useEffect } from "react";
import useGetDocList from "@/hooks/useGetDocList";
import { useLocation, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { Divider } from "antd";
import type { ColumnsType } from "antd/es/table";
import APIField from "./components/APIField";
import TypeField from "./components/TypeField";

interface DataType {
  key: React.Key;
  argument: string;
  describe: string;
  type: string;
  default: string;
}

const columns: ColumnsType<DataType> = [
  {
    title: "參數",
    dataIndex: "argument",
  },
  {
    title: "說明",
    dataIndex: "describe",
  },
  {
    title: "型別",
    dataIndex: "type",
  },
  {
    title: "預設值",
    dataIndex: "default",
  },
];

const ContentContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 48px;
  width: 100%;
  background-color: white;
  gap: 40px;
  margin-left: 200px;
`;

const Space = styled.div`
  padding: 36px;
`;

const Doc: React.FC = () => {
  const location = useLocation();
  const { hash } = location;
  const { apiList = [], typeList = [] } = useGetDocList();

  useEffect(() => {
    const element = document.getElementById(hash.substring(1));
    if (element) {
      element.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }
  }, [hash]);

  // Your component code here
  return (
    <>
      <ContentContainer>
        <h1>API</h1>
        {apiList.map((apiData): any => {
          const {
            id = "",
            fullMethod,
            methodDescribe,
            version,
            propertyList,
            returnValue,
          } = apiData;
          return (
            <APIField
              id={id}
              fullMethod={fullMethod}
              methodDescribe={methodDescribe}
              version={version}
              propertyList={propertyList}
              returnValue={returnValue}
            ></APIField>
          );
        })}

        <div>
          <Divider />
          <h1>Porperty Type</h1>
        </div>
        {typeList.map((type): any => {
          const { id = "", name, methodDescribe, fields } = type;
          return (
            <TypeField
              id={id}
              name={name}
              methodDescribe={methodDescribe}
              fields={fields}
            ></TypeField>
          );
        })}
        <Space />
      </ContentContainer>
    </>
  );
};

export default Doc;
