import { configureStore, getDefaultMiddleware } from "@reduxjs/toolkit";
import { combineReducers } from "redux";
import maskReducer from "@/toolkitRedux/slice/maskSlice";

const SYSTEM_NAME: string = process.env.REACT_APP_NAME ?? "test";

const reducers = combineReducers({
  maskReducer,
});

const store = configureStore({
  reducer: { [SYSTEM_NAME]: reducers },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false,
    }),
});

export function setupStore(preloadedState: any) {
  return configureStore({
    reducer: { [SYSTEM_NAME]: reducers },
    preloadedState: {
      [SYSTEM_NAME]: preloadedState,
    },
  });
}

export type AppDispatch = typeof store.dispatch;


export default store;
