import styled from "styled-components";
import { Button, Layout, Menu } from "antd";
import React, { useEffect, useState } from "react";
import type { MenuProps } from "antd";
import useGetDocList from "@/hooks/useGetDocList";
import { ReadOutlined, SettingOutlined } from "@ant-design/icons";
import { useLocation, useNavigate } from "react-router-dom";

const { Sider } = Layout;

const MySidebar = styled(Sider)`
  background: white !important;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  position: fixed !important;
  top: 48px;
  height: calc(100% - 48px);
  overflow: auto;
  .ant-layout-sider-children {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 8px;
    > div:nth-child(2) {
      border-top: solid #f0f0f0 2px;
    }
  }
`;

type MenuItem = Exclude<
  Required<MenuProps>["items"],
  { type: "divider" }
>[number];

function getItem(
  label: React.ReactNode,
  key: string,
  icon?: React.ReactNode,
  children?: MenuItem[],
  disabled?: boolean
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
    disabled,
  } as MenuItem;
}

export default function LayoutSidebar() {
  const navigate = useNavigate();
  const location = useLocation();
  const { pathname = "" } = location;
  const { apiList, typeList } = useGetDocList();
  const [selectedKeys, setSelectedKeys] = useState<string[]>(["profile"]);

  const myItems: MenuItem[] = [
    getItem("快速開始", "quickStart", null),
    getItem("API", "doc", null, [
      ...apiList.map((item: any) => {
        const { name, id } = item;
        return getItem(name, id);
      }),
      getItem("Type", "typeAnchor", null, undefined, true),
      ...typeList.map((item: any) => {
        const { name, id } = item;
        return getItem(name, id);
      }),
    ]),
  ];

  const handleMenuSelect = (props: any) => {
    const { key = "", keyPath = "" } = props;
    if (keyPath.length > 1 && keyPath[keyPath.length - 1] === "doc") {
      navigate(`${keyPath[1]}#${keyPath[0]}`);
      const element = document.getElementById(keyPath[0]);
      if (element) {
        element.scrollIntoView({
          behavior: "smooth",
          block: "start",
        });
      }
    } else {
      navigate(`${key}`);
      window.scrollTo(0, 0);
    }
  };

  useEffect(() => {}, []);

  useEffect(() => {
    const pathname = location.pathname.split("/")[1];
    const findMenuItem = (items: MenuItem[]): MenuItem | undefined => {
      for (const item of items) {
        if (!item) {
          return;
        }
        if ("type" in item && item.type === "divider") {
          continue;
        }
        if (pathname === `${item.key}`) {
          return item;
        }
        if ((item as any).children) {
          const matchedChild = findMenuItem((item as any).children);
          if (matchedChild) {
            return matchedChild;
          }
        }
      }
      return undefined;
    };
    const myMenuItems = myItems;
    const matchedItem = findMenuItem(myMenuItems);
    const matchedKey = matchedItem?.key;

    if (matchedKey) {
      setSelectedKeys([matchedKey as string]);
      // console.log('有對應到選單')
    } else {
      // console.log("沒對應到選單")
    }
  }, [location.pathname]);

  return (
    <>
      <MySidebar trigger={null}>
        <Menu
          defaultOpenKeys={[`doc`]}
          selectedKeys={selectedKeys}
          mode="inline"
          items={myItems}
          onSelect={handleMenuSelect}
        />
      </MySidebar>
    </>
  );
}
